<script >import { createEventDispatcher } from "svelte";
import { TOKENS_VARIATION_POPOVER } from "../../../types/variations";
import { click_inside } from "../../../actions/click_inside";
import { click_outside } from "../../../actions/click_outside";
import { forward_actions } from "../../../actions/forward_actions";
import { action_exit } from "../../../actions/keybind";
import { lost_focus } from "../../../actions/lost_focus";
import { map_data_attributes, map_global_attributes } from "../../../util/attributes";
import { can_focus } from "../../../util/element";
import PopoverGroup from "./PopoverGroup.svelte";
const dispatch = createEventDispatcher();
export let actions = undefined;
export let element = undefined;
let _class = "";
export { _class as class };
export let logic_id = "";
export let logic_state = false;
// HACK: Focus management properties need default invalid references so
// the Context Stores get created. Due to `<element bind:this>` / `<Component bind:element>`
// taking a frame to be initialized
export let focus_target = null;
export let dismissible = undefined;
export let loading = undefined;
export let once = undefined;
export let variation = undefined;
function on_click_inside(event) {
    if (once)
        logic_state = false;
    else if (_is_control) {
        const { target } = event;
        if (target && !can_focus(target))
            logic_state = false;
    }
}
function on_dismiss() {
    if (!logic_state)
        return;
    if (_is_control || (_is_popover && dismissible))
        logic_state = false;
}
function on_focus_in(event) {
    if (logic_state || !_is_control)
        return;
    logic_state = true;
}
function on_group_change(event) {
    dispatch(logic_state ? "active" : "dismiss");
}
function on_input_change(event) {
    logic_state = event.target.checked;
}
$: _is_control = variation === TOKENS_VARIATION_POPOVER.control;
$: _is_popover = !variation || variation === TOKENS_VARIATION_POPOVER.popover;
$: _label = logic_id ? `label[for="${logic_id}"]` : undefined;
</script>

<svelte:window use:action_exit={{on_bind: () => on_dismiss}} />

{#if _is_popover && logic_id}
    <input
        role="presentation"
        class="popover--state"
        id={logic_id}
        type="checkbox"
        bind:checked={logic_state}
        on:change={on_input_change}
    />
{/if}

<div
    bind:this={element}
    {...map_global_attributes($$restProps)}
    class="popover {_class}"
    {...map_data_attributes({variation})}
    use:forward_actions={{actions}}
    use:click_inside={{
        ignore: _label,
        on_click_inside: on_click_inside,
    }}
    use:click_outside={{
        ignore: _label,
        on_click_outside: on_dismiss,
    }}
    use:lost_focus={{
        enabled: _is_control || _is_popover,
        ignore: _label,
        on_lost_focus: on_dismiss,
    }}
    on:focusin={on_focus_in}
    on:click
    on:contextmenu
    on:dblclick
    on:focusin
    on:focusout
    on:keydown
    on:keyup
    on:pointercancel
    on:pointerdown
    on:pointerenter
    on:pointerleave
    on:pointermove
    on:pointerout
    on:pointerup
>
    <PopoverGroup
        variation={variation ?? TOKENS_VARIATION_POPOVER.popover}
        {focus_target}
        {loading}
        {logic_id}
        bind:logic_state
        on:change={on_group_change}
    >
        <slot />
    </PopoverGroup>
</div>
