import { get, writable } from "svelte/store";
/**
 *
 * @param default_value
 * @returns
 */
export function state(default_value = "") {
    const store = writable(default_value);
    const { set, subscribe, update } = store;
    return {
        set,
        subscribe,
        update,
        clear() {
            set("");
        },
        push(value) {
            let cache = get(store);
            if (cache.includes(value))
                return;
            if (cache) {
                if (typeof cache === "string")
                    cache = [cache, value];
                else
                    cache.push(value);
                set([...cache]);
            }
            else {
                cache = value;
                set(value);
            }
        },
        remove(value) {
            let cache = get(store);
            if (cache) {
                if (typeof cache === "string") {
                    if (cache === value)
                        set("");
                }
                else {
                    const index = cache.indexOf(value);
                    if (index > -1) {
                        cache = [...cache];
                        cache.splice(index, 1);
                        set(cache.length > 1 ? cache : cache[0]);
                    }
                }
            }
        },
    };
}
