<script>
    import {Box, Button, Center, Dot, Ellipsis, Table, Text} from "@kahi-ui/framework";
    import {onMount} from "svelte";
    import axios from "axios";
    import {access_token, userinfo} from "../stores";
    import {toast} from "svelte-toastify";

    export let isAdmin;

    let tasks = 0;

    onMount(() => {
        access_token.subscribe((value) => {
            if (value.trim() !== "") {
                axios.get("http://localhost:8081/api/task/get?a=1", {headers: {"Authorization": `Bearer ${value}`}})
                    .then((response) => {
                        tasks = response.data;
                    })
                    .catch((error) => {
                        toast.error("Ошибка при получении списка задач")
                        console.log(error)
                    })
            }
        });
    })


    onMount(() => {

    })
</script>

<main>
        {#if tasks === 0}
            <Box height="prose">
                <Center>
                    <Text is="strong">Загрузка списка задач</Text>
                    <br /><br />
                    <Ellipsis><Dot palette="accent"></Dot></Ellipsis>
                </Center>
            </Box>
        {:else if tasks.length === 0}
            <Text is="strong">Список задач пуст</Text>
        {:else}
            <Table.Container variation="borders">
                <Table.Row>
                    <Table.Heading>Номер задачи</Table.Heading>
                    <Table.Heading>Наименование задачи</Table.Heading>
                    <Table.Heading>Описание задачи</Table.Heading>
                    <!--{#if isAdmin}
                        <Table.Heading></Table.Heading>
                    {/if}-->
                </Table.Row>

                <Table.Section>
                    {#each tasks as task}
                        <Table.Row>
                            <Table.Column><Text is="strong"><a href={`/#/task/${task.id}?a=1`}>{task.id}</a></Text></Table.Column>
                            <Table.Column><Text is="strong"><a href={`/#/task/${task.id}?a=1`}>{task.title}</a></Text></Table.Column>
                            <Table.Column><Text>{task.description}</Text></Table.Column>
                            <!--{#if isAdmin}
                                <Table.Column><Button palette="alert">Редактировать</Button></Table.Column>
                            {/if}-->
                        </Table.Row>


                    {/each}
                </Table.Section>
            </Table.Container>
        {/if}
</main>

