<script >import { map_aria_attributes, map_attributes, map_data_attributes, map_global_attributes, } from "../../../util/attributes";
import { forward_actions } from "../../../actions/forward_actions";
export let actions = undefined;
export let element = undefined;
let _class = "";
export { _class as class };
export let active = undefined;
export let disabled = undefined;
export let palette = undefined;
</script>

<button
    bind:this={element}
    {...map_global_attributes($$restProps)}
    class="menu--item {_class}"
    {...map_data_attributes({palette})}
    {...map_aria_attributes({pressed: active})}
    {...map_attributes({disabled})}
    use:forward_actions={{actions}}
    on:click
    on:contextmenu
    on:dblclick
    on:focusin
    on:focusout
    on:keydown
    on:keyup
    on:pointercancel
    on:pointerdown
    on:pointerenter
    on:pointerleave
    on:pointermove
    on:pointerout
    on:pointerup
>
    <slot />
</button>
