import { action_activate } from "./keybind";
/**
 * Emulates Button behavior on the given element, e.g. spacebar to click
 *
 * @param element
 * @param options
 * @returns
 */
export const behavior_button = (element, options) => {
    let { enabled } = options;
    let activate_handle = null;
    function on_bind(event) {
        if (!event.detail.active)
            return;
        event.preventDefault();
        element.click();
    }
    function attach_events() {
        if (!activate_handle)
            activate_handle = action_activate(element, { on_bind });
    }
    function detach_events() {
        if (activate_handle) {
            activate_handle.destroy();
            activate_handle = null;
        }
    }
    if (enabled)
        attach_events();
    return {
        destroy() {
            if (enabled)
                detach_events();
        },
        update(options) {
            ({ enabled } = options);
            if (enabled)
                attach_events();
            else
                detach_events();
        },
    };
};
