<script >import { forward_actions } from "../../../actions/forward_actions";
import { map_aria_attributes, map_global_attributes } from "../../../util/attributes";
import FormGroup, { CONTEXT_FORM_ID } from "./FormGroup.svelte";
export let actions = undefined;
export let element = undefined;
let _class = "";
export { _class as class };
export let active = undefined;
export let disabled = undefined;
let _for = "";
export { _for as for };
const _form_id = CONTEXT_FORM_ID.get();
$: _logic_for = _form_id ? $_form_id : _for;
</script>

<label
    bind:this={element}
    {...map_global_attributes($$restProps)}
    class="form--label {_class}"
    {...map_aria_attributes({disabled, pressed: active})}
    for={_logic_for}
    use:forward_actions={{actions}}
    on:click
    on:contextmenu
    on:dblclick
    on:focusin
    on:focusout
    on:keydown
    on:keyup
    on:pointercancel
    on:pointerdown
    on:pointerenter
    on:pointerleave
    on:pointermove
    on:pointerout
    on:pointerup
>
    {#if !_form_id && _for}
        <FormGroup logic_id={_for}>
            <slot />
        </FormGroup>
    {:else}
        <slot />
    {/if}
</label>
