<script>
    import {access_token, keycloak, userinfo} from "../stores";
    import axios from "axios";
    import {toast} from "svelte-toastify";
    import {onMount} from "svelte";
    import {Button, Code, DataSelect, Dot, Ellipsis, Table, Text, TextInput} from "@kahi-ui/framework";

    export let taskId;

    let taskInfo = 0;
    let languages = [];

    onMount(() => {
        access_token.subscribe((token) => {
            if (token.trim() !== "") {
                axios.get(`http://localhost:8081/api/task/get/${taskId}?a=1`, {headers: {"Authorization": `Bearer ${token}`}})
                    .then((response) => {
                        taskInfo = response.data
                    })
                    .catch((error) => {
                        console.log(error);
                        toast.error(`Возникла ошибка при попытке получить задание ${taskId}`);
                    });
                axios.get("http://localhost:8081/api/runner/languages?a=1", {headers: {"Authorization": `Bearer ${token}`}})
                    .then((response) => {
                        languages = response.data.map((element) => {
                            return {text: element, id: element, palette: "accent"}
                        });
                        console.log(languages)
                    })
                    .catch((error) => {
                        console.log(error);
                        toast.error("Возникла ошибка при получении списка языков")
                    })
            }
        })
    })

    let taskRequest = {
        taskId: 0,
        username: "",
        sourceCode: "",
        language: "",
        languageVersion: ""
    }

    userinfo.subscribe((info) => {
        taskRequest.username = info.sub;
    })

    function sendTask() {
        taskRequest.taskId = taskInfo.id;
        taskRequest.languageVersion = taskRequest.language.split(":")[1];
        taskRequest.language = taskRequest.language.split(":")[0];
        axios.post("http://localhost:8081/api/checker/run?a=1", taskRequest, {headers: {"Authorization": `Bearer ${$access_token}`}})
            .then((response) => {
                toast.success("Решение отправлено")
            })
            .catch((error) => {
                toast.error("Возникла ошибка при отправке решения")
                console.log(error)
            });
    }
</script>

<main>
    {#if taskInfo === 0}
        <Text is="strong">Загрузка заданий</Text>
        <Ellipsis><Dot palette="accent" /></Ellipsis>

    {:else}
        <Text is="strong">{taskId}. {taskInfo.title}</Text>
        <Text>{taskInfo.description}</Text>
        <Table.Container>
            <Table.Row>
                <Table.Heading>Ввод</Table.Heading>
                <Table.Heading>Вывод</Table.Heading>
            </Table.Row>
            <Table.Section>
                {#each taskInfo.tests as test}
                    <Table.Row>
                        <Table.Column><Code style="white-space: pre-line">{test.input}</Code></Table.Column>
                        <Table.Column><Code style="white-space: pre-line">{test.expectedOutput}</Code></Table.Column>
                    </Table.Row>
                {/each}
            </Table.Section>
        </Table.Container>
        <Text>Выберите язык программирования</Text>
        <DataSelect
            items={languages}
            logic_name="language-select"
            placeholer="Выберите язык программирования"
            palette="accent"
            searching=""
            bind:logic_state={taskRequest.language}
            />
        <br />
        <Text>Введите код программы</Text>
        <TextInput is="textarea" style="height: auto; min-height: 60px; max-height: 400px" resizable bind:value={taskRequest.sourceCode}/>
        <Button on:click={sendTask}>Отправить решение</Button>
    {/if}
</main>

