<script >import { forward_actions } from "../../../actions/forward_actions";
import { map_data_attributes, map_global_attributes } from "../../../util/attributes";
export let actions = undefined;
export let element = undefined;
let _class = "";
export { _class as class };
export let orientation = undefined;
export let variation = undefined;
export let alignment = undefined;
export let alignment_x = undefined;
export let alignment_y = undefined;
export let spacing = undefined;
export let spacing_x = undefined;
export let spacing_y = undefined;
</script>

<div
    bind:this={element}
    {...map_global_attributes($$restProps)}
    class="stack {_class}"
    {...map_data_attributes({
        alignment,
        "alignment-x": alignment_x,
        "alignment-y": alignment_y,
        orientation,
        spacing,
        "spacing-x": spacing_x,
        "spacing-y": spacing_y,
        variation,
    })}
    use:forward_actions={{actions}}
    on:click
    on:contextmenu
    on:dblclick
    on:focusin
    on:focusout
    on:keydown
    on:keyup
    on:pointercancel
    on:pointerdown
    on:pointerenter
    on:pointerleave
    on:pointermove
    on:pointerout
    on:pointerup
>
    <slot />
</div>
