<script>

    import {onMount} from "svelte";
    import {access_token, userinfo} from "../stores";
    import axios from "axios";
    import {toast} from "svelte-toastify";
    import {DataTable} from "@kahi-ui/framework";

    let sents = [];

    const columns = [
        {
            text: "ID",
            key: "id",
            sorting: true
        },
        {
            text: "ID задачи",
            key: "taskId",
            sorting: true
        },
        {
            text: "Имя пользователя",
            key: "username",
            sorting: true
        },
        {
            text: "Язык программирования",
            key: "language",
            sorting: true
        },
        {
            text: "Статус",
            key: "testStatus",
            sorting: true
        }
    ]

    onMount(() => {
        userinfo.subscribe(() => {
            loadData($access_token);
        })
    })

    function loadData(token) {
        if (token.trim() !== "" && $userinfo !== {} && $userinfo.sub !== undefined) {
            axios.get(`http://localhost:8081/api/checker/get/user/${$userinfo.sub}?a=1`)
                .then((response) => {
                    console.log(response.data)
                    sents = response.data.map((sent) => {return {id: sent.id, taskId: sent.taskId, username: sent.username, language: sent.language + ":" + sent.languageVersion, testStatus: sent.testStatus}})
                })
                .catch((error) => {
                    toast.error("Возникла ошибка при получении списка отправок")
                    console.log(error)
                })
        } else {
            setTimeout(() => loadData(token), 500);
        }
    }
</script>

<main>
    <DataTable columns={columns} rows={sents} palette="accent"/>
</main>