<script >import { behavior_button } from "../../../actions/behavior_button";
import { forward_actions } from "../../../actions/forward_actions";
import { map_aria_attributes, map_data_attributes, map_global_attributes, } from "../../../util/attributes";
import FormGroup from "../../interactables/form/FormGroup.svelte";
export let actions = undefined;
export let element = undefined;
let _class = "";
export { _class as class };
export let tabindex = 0;
export let active = undefined;
export let disabled = undefined;
let _for = "";
export { _for as for };
export let palette = undefined;
// HACK: Svelte has `tabindex` typed as `number | undefined` unless
// you pass a string literal into the markup
$: _tabindex = tabindex;
</script>

<FormGroup logic_id={_for}>
    <label
        bind:this={element}
        {...map_global_attributes($$restProps)}
        role="button"
        class="menu--item {_class}"
        {...map_data_attributes({palette})}
        {...map_aria_attributes({disabled, pressed: active})}
        for={_for}
        tabindex={_tabindex}
        use:behavior_button={{enabled: true}}
        use:forward_actions={{actions}}
        on:click
        on:contextmenu
        on:dblclick
        on:focusin
        on:focusout
        on:keydown
        on:keyup
        on:pointercancel
        on:pointerdown
        on:pointerenter
        on:pointerleave
        on:pointermove
        on:pointerout
        on:pointerup
    >
        <slot />
    </label>
</FormGroup>
