<script>
    import {userinfo, access_token, keycloak} from "../stores";
    import {Anchor, Divider, Omni, Menu, Text} from "@kahi-ui/framework";
    import axios from "axios";
    import {onMount} from "svelte";
    import Keycloak from "keycloak-js";
    import {toast} from "svelte-toastify";
    import {replace} from "svelte-spa-router";

    const keycloakInitOptions = {
        url: 'http://localhost:8080/',
        realm: 'simple-contester',
        clientId: 'sc_frontend',
        onLoad: 'login-required'
    }

    let keycloak_instance = new Keycloak(keycloakInitOptions)

    keycloak_instance.init({onLoad: keycloakInitOptions.onLoad})
        .then((auth) => {
            if (auth) {
                axios.get("http://localhost:8080/realms/simple-contester/protocol/openid-connect/userinfo", {
                    headers: {"Authorization": `Bearer ${keycloak_instance.token}`}
                }).then((response) => {
                    userinfo.set(response.data);
                    console.log(response)
                }).catch((error) => {
                    toast.error("Ошибка во время связи с сервисом Keycloak");
                    console.log(error)
                });
                access_token.set(keycloak_instance.token);
                logout_url = keycloak_instance.createLogoutUrl("http://localhost:8081");
            }
        })
        .catch((error) => {
            console.log(error)
        })

    setInterval(() => {
        keycloak_instance.updateToken(180).then(() => {access_token.set(keycloak_instance.token)}).catch((error) => {
            toast.error("Can't update access token");
            console.log(error)
        })
    }, 60 * 1000)


    keycloak.set(keycloak_instance)

    let flag = true;
    let token;

    let isAdmin = false;

    onMount(() => {
        access_token.subscribe((value) => {
            if (value.trim() !== "") {
                axios.get("http://localhost:8081/api/checker/isAdmin?a=1", {headers: {"Authorization": `Bearer ${$access_token}`}})
                    .then((response) => {
                        isAdmin = response.data === "admin";
                    })
                    .catch((err) => {
                        isAdmin = false
                    })
            }
        })

    })

    let logout_url;
</script>


<Omni.Container palette="dark" width="100">
    <Omni.Header>
        <a href="/#/?a=1">Simple Contester</a>
    </Omni.Header>

    <Omni.Section>
        <Menu.Container
                orientation="horizontal">
            <Menu.Button><a href="/#/?a=1">Банк задач</a></Menu.Button>
            <Menu.Button><a href="/#/sends?a=1">Мои отправки</a></Menu.Button>
            {#if isAdmin}
            <Menu.Button><a href="/#/admin?a=1">Админ-панель</a></Menu.Button>
            {/if}
        </Menu.Container>
    </Omni.Section>

    <Omni.Footer>
        <Menu.Container
                orientation="horizontal">
            {#if $userinfo !== null}
                <Text is="strong" style="padding-top: 3px">{$userinfo.name}</Text>
                {#if flag}
                    <Menu.Button on:click={() => {
                        access_token.set("");
                        userinfo.set({});
                        $keycloak.logout();
                        keycloak.set({});
                        console.log(logout_url)
                    }
                    }
                    >Выйти</Menu.Button>
                {/if}
            {/if}
        </Menu.Container>
    </Omni.Footer>
</Omni.Container>
