<script >import { forward_actions } from "../../../actions/forward_actions";
import { map_aria_attributes, map_attributes, map_data_attributes, map_global_attributes, } from "../../../util/attributes";
import FormLabel from "../form/FormLabel.svelte";
import FormGroup, { CONTEXT_FORM_ID, CONTEXT_FORM_NAME, CONTEXT_FORM_STATE, } from "../form/FormGroup.svelte";
export let actions = undefined;
export let element = undefined;
let _class = "";
export { _class as class };
export let id = "";
export let name = "";
export let active = undefined;
export let disabled = undefined;
export let state = undefined;
export let value = "";
export let palette = undefined;
export let sizing = undefined;
export let variation = undefined;
const _form_id = CONTEXT_FORM_ID.get();
const _form_name = CONTEXT_FORM_NAME.get();
const _form_state = CONTEXT_FORM_STATE.get();
function on_change(event) {
    state = event.target.checked;
}
$: _id = _form_id ? $_form_id : id;
$: _name = _form_name ? $_form_name : name;
$: if (_form_state && value) {
    if (state)
        _form_state.push(value);
    else
        _form_state.remove(value);
}
$: if (_form_state && $_form_state && value)
    state = $_form_state.includes(value);
</script>

{#if $$slots["default"]}
    <FormGroup logic_id={id}>
        <FormLabel>
            <input
                bind:this={element}
                {...map_global_attributes($$restProps)}
                type="checkbox"
                class="check {_class}"
                {...map_data_attributes({palette, sizing, variation})}
                {...map_aria_attributes({pressed: active})}
                {...map_attributes({
                    disabled,
                    id,
                    name,
                    value,
                })}
                checked={state}
                use:forward_actions={{actions}}
                on:click
                on:contextmenu
                on:dblclick
                on:focusin
                on:focusout
                on:keydown
                on:keyup
                on:pointercancel
                on:pointerdown
                on:pointerenter
                on:pointerleave
                on:pointermove
                on:pointerout
                on:pointerup
                on:change={on_change}
                on:change
                on:input
            />

            <slot />
        </FormLabel>
    </FormGroup>
{:else}
    <input
        bind:this={element}
        {...map_global_attributes($$restProps)}
        type="checkbox"
        class="check {_class}"
        {...map_data_attributes({palette, sizing, variation})}
        {...map_aria_attributes({pressed: active})}
        {...map_attributes({
            disabled,
            id: _id,
            name: _name,
            value,
        })}
        checked={state}
        use:forward_actions={{actions}}
        on:click
        on:contextmenu
        on:dblclick
        on:focusin
        on:focusout
        on:keydown
        on:keyup
        on:pointercancel
        on:pointerdown
        on:pointerenter
        on:pointerleave
        on:pointermove
        on:pointerout
        on:pointerup
        on:change={on_change}
        on:change
        on:input
    />
{/if}
