<script >import { forward_actions } from "../../../actions/forward_actions";
import { map_global_attributes } from "../../../util/attributes";
import { range } from "../../../util/functional";
import Animation from "../../utilities/animation/Animation.svelte";
export let actions = undefined;
export let element = undefined;
let _class = "";
export { _class as class };
export let animation = undefined;
export let iterations = undefined;
$: _iterations = typeof iterations === "string" ? parseInt(iterations) : iterations ?? 3;
</script>

<span
    bind:this={element}
    {...map_global_attributes($$restProps)}
    class="ellipsis {_class}"
    use:forward_actions={{actions}}
    on:click
    on:contextmenu
    on:dblclick
    on:focusin
    on:focusout
    on:keydown
    on:keyup
    on:pointercancel
    on:pointerdown
    on:pointerenter
    on:pointerleave
    on:pointermove
    on:pointerout
    on:pointerup
>
    {#each range(0, _iterations - 1) as index}
        <Animation
            is="span"
            class="ellipsis--item"
            animation={animation ?? "pulse"}
            delay={(index + 1) / _iterations - 1}
        >
            <slot {index}>.</slot>
        </Animation>
    {/each}
</span>
