            maxlength: max,
            minlength: min,
            name: _name,
            placeholder,
            readonly,
            required,
            rows: span_y,
            spellcheck: spell_check,
        })}
        use:mask_input={{enabled: mask, on_mask, pattern}}
        bind:value
        use:forward_actions={{actions}}
        on:click
        on:contextmenu
        on:dblclick
        on:focusin
        on:focusout
        on:keydown
        on:keyup
        on:pointercancel
        on:pointerdown
        on:pointerenter
        on:pointerleave
