<script >import { forward_actions } from "../../../actions/forward_actions";
import { map_data_attributes, map_global_attributes } from "../../../util/attributes";
export let actions = undefined;
export let element = undefined;
export let is = "div";
let _class = "";
export { _class as class };
export let spacing = undefined;
export let spacing_x = undefined;
export let spacing_y = undefined;
</script>

{#if is === "span"}
    <span
        bind:this={element}
        {...map_global_attributes($$restProps)}
        class="spacer {_class}"
        {...map_data_attributes({
            spacing,
            "spacing-x": spacing_x,
            "spacing-y": spacing_y,
        })}
        use:forward_actions={{actions}}
        on:click
        on:contextmenu
        on:dblclick
        on:focusin
        on:focusout
        on:keydown
        on:keyup
        on:pointercancel
        on:pointerdown
        on:pointerenter
        on:pointerleave
        on:pointermove
        on:pointerout
        on:pointerup
    >
        <slot />
    </span>
{:else}
    <div
        bind:this={element}
        {...map_global_attributes($$restProps)}
        class="spacer {_class}"
        {...map_data_attributes({
            spacing,
            "spacing-x": spacing_x,
            "spacing-y": spacing_y,
        })}
        use:forward_actions={{actions}}
        on:click
        on:contextmenu
        on:dblclick
        on:focusin
        on:focusout
        on:keydown
        on:keyup
        on:pointercancel
        on:pointerdown
        on:pointerenter
        on:pointerleave
        on:pointermove
        on:pointerout
        on:pointerup
    >
        <slot />
    </div>
{/if}
