<script >import { behavior_button } from "../../../actions/behavior_button";
import { forward_actions } from "../../../actions/forward_actions";
import { map_aria_attributes, map_attributes, map_data_attributes, map_global_attributes, } from "../../../util/attributes";
export let actions = undefined;
export let element = undefined;
let _class = "";
export let tabindex = 0;
export { _class as class };
export let is = undefined;
export let active = undefined;
export let disabled = undefined;
export let type = undefined;
export let value = "";
export let download = undefined;
export let href = undefined;
export let rel = undefined;
export let target = undefined;
let _for = undefined;
export { _for as for };
export let palette = undefined;
export let radius = undefined;
export let shape = undefined;
export let sizing = undefined;
export let variation = undefined;
// HACK: Svelte has `tabindex` typed as `number | undefined` unless
// you pass a string literal into the markup
$: _tabindex = tabindex;
</script>

{#if is === "a"}
    <a
        bind:this={element}
        {...map_global_attributes($$restProps)}
        role="button"
        class="button {_class}"
        {...map_data_attributes({palette, radius, shape, sizing, variation})}
        {...map_aria_attributes({active, disabled})}
        {download}
        {href}
        {rel}
        {target}
        use:forward_actions={{actions}}
        on:click
        on:contextmenu
        on:dblclick
        on:focusin
        on:focusout
        on:keydown
        on:keyup
        on:pointercancel
        on:pointerdown
        on:pointerenter
        on:pointerleave
        on:pointermove
        on:pointerout
        on:pointerup
    >
        <slot />
    </a>
{:else if is === "label"}
    <label
        bind:this={element}
        {...map_global_attributes($$restProps)}
        role="button"
        class="button {_class}"
        for={_for}
        tabindex={_tabindex}
        {...map_data_attributes({palette, radius, shape, sizing, variation})}
        {...map_aria_attributes({disabled, pressed: active})}
        use:behavior_button={{enabled: true}}
        use:forward_actions={{actions}}
        on:click
        on:contextmenu
        on:dblclick
        on:focusin
        on:focusout
        on:keydown
        on:keyup
        on:pointercancel
        on:pointerdown
        on:pointerenter
        on:pointerleave
        on:pointermove
        on:pointerout
        on:pointerup
    >
        <slot />
    </label>
{:else if is === "input"}
    {#if type === "reset"}
        <input
            bind:this={element}
            {...map_global_attributes($$restProps)}
            class="button {_class}"
            type="reset"
            {...map_data_attributes({palette, radius, shape, sizing, variation})}
            {...map_aria_attributes({pressed: active})}
            {...map_attributes({disabled, value})}
            use:forward_actions={{actions}}
            on:click
            on:contextmenu
            on:dblclick
            on:focusin
            on:focusout
            on:keydown
            on:keyup
            on:pointercancel
            on:pointerdown
            on:pointerenter
            on:pointerleave
            on:pointermove
            on:pointerout
            on:pointerup
        />
    {:else if type === "submit"}
        <input
            bind:this={element}
            {...map_global_attributes($$restProps)}
            class="button {_class}"
            type="submit"
            {...map_data_attributes({palette, radius, shape, sizing, variation})}
            {...map_aria_attributes({pressed: active})}
            {...map_attributes({disabled, value})}
            use:forward_actions={{actions}}
            on:click
            on:contextmenu
            on:dblclick
            on:focusin
            on:focusout
            on:keydown
            on:keyup
            on:pointercancel
            on:pointerdown
            on:pointerenter
            on:pointerleave
            on:pointermove
            on:pointerout
            on:pointerup
        />
    {:else}
        <input
            bind:this={element}
            {...map_global_attributes($$restProps)}
            class="button {_class}"
            type="button"
            {...map_data_attributes({palette, radius, shape, sizing, variation})}
            {...map_aria_attributes({pressed: active})}
            {...map_attributes({disabled, value})}
            use:forward_actions={{actions}}
            on:click
            on:contextmenu
            on:dblclick
            on:focusin
            on:focusout
            on:keydown
            on:keyup
            on:pointercancel
            on:pointerdown
            on:pointerenter
            on:pointerleave
            on:pointermove
            on:pointerout
            on:pointerup
        />
    {/if}
{:else}
    <button
        bind:this={element}
        {...map_global_attributes($$restProps)}
        class="button {_class}"
        {...map_data_attributes({palette, radius, shape, sizing, variation})}
        {...map_aria_attributes({pressed: active})}
        {...map_attributes({disabled})}
        use:forward_actions={{actions}}
        on:click
        on:contextmenu
        on:dblclick
        on:focusin
        on:focusout
        on:keydown
        on:keyup
        on:pointercancel
        on:pointerdown
        on:pointerenter
        on:pointerleave
        on:pointermove
        on:pointerout
        on:pointerup
    >
        <slot />
    </button>
{/if}
