import { query_focusable_element } from "../util/element";
/**
 * When [[IAutoFocusOptions.enabled]] is set to `true`, focuses the first found
 * focusable element or the [[IAutoFocusOptions.target]] option if configured. Returns
 * focus to the previously focused element when disabled
 *
 * @param element
 * @param options
 * @returns
 */
export const auto_focus = (element, options) => {
    let { enabled, target } = options;
    let previous_element = null;
    function capture_focus() {
        const queried_element = query_target();
        if (queried_element) {
            previous_element = document.activeElement;
            queried_element.focus();
        }
    }
    function query_target() {
        if (target) {
            return typeof target === "string" ? element.querySelector(target) : target;
        }
        return query_focusable_element(element);
    }
    async function restore_focus() {
        if (previous_element) {
            previous_element.focus();
            previous_element = null;
            return;
        }
        const active_element = document.activeElement;
        if (active_element)
            active_element.blur();
    }
    if (enabled)
        capture_focus();
    return {
        update(options) {
            ({ enabled, target } = options);
            if (enabled)
                capture_focus();
            else
                restore_focus();
        },
    };
};
