<script >import { format_css_variables, map_data_attributes, map_global_attributes, } from "../../../util/attributes";
export let element = undefined;
let _class = "";
export let style = "";
export { _class as class };
export let is = undefined;
export let animation;
export let delay = undefined;
export let duration = undefined;
export let iterations = undefined;
export let variation = undefined;
$: _variables = format_css_variables({ delay, duration, iterations });
</script>

{#if is === "span"}
    <span
        bind:this={element}
        {...map_global_attributes($$restProps)}
        class="animation {_class}"
        {...map_data_attributes({animation, variation})}
        on:animationend
        on:animationstart
        style={_variables ? `${style}${_variables};` : style}
    >
        <slot />
    </span>
{:else}
    <div
        bind:this={element}
        {...map_global_attributes($$restProps)}
        class="animation {_class}"
        {...map_data_attributes({animation, variation})}
        on:animationend
        on:animationstart
        style={_variables ? `${style}${_variables};` : style}
    >
        <slot />
    </div>
{/if}
