<script>

import SC_Header from "../components/SC_Header.svelte";
import {Container} from "@kahi-ui/framework/components/display/list";
import {Text, TextInput, Form, Button, Switch} from "@kahi-ui/framework";
import axios from "axios";
import {access_token} from "../stores";
import {toast} from "svelte-toastify";
import {replace} from "svelte-spa-router";

let task = {
    title: '',
    description: '',
    tests: []
}

let token;

access_token.subscribe((value) => {
    if (value !== "") {
        token = value;
    }
})

function createTask() {
    console.log(token);
    axios.post("http://localhost:8081/api/task/create?a=1", task, {headers: {"Authorization": `Bearer ${token}`}})
        .then((response) => {
            toast.success("Задача успешно создана")
            replace("/admin?a=1")
        })
        .catch((error) => {
            toast.error("При создании задачи возникла ошибка")
            console.log(error)
        })
}
</script>

<style>
    .test-input {
        margin: 0 10px;
    }
</style>

<main>
    <SC_Header />
    <Container>
        <Text is="strong">Создание новой задачи</Text>
        <br/><br/>
        <Form.Label>
            Название задачи
        </Form.Label>
        <TextInput bind:value={task.title} />
        <Form.Label>
            Описание задачи
        </Form.Label>
        <TextInput bind:value={task.description} is="textarea" />
        <Form.Label>Тесты:</Form.Label>
        <Container>
            {#each task.tests as test, i}
                <Container>
                    <Text>Тест {i + 1}.</Text>
                    <Container style="display: inline-flex">
                        <div class="test-input">
                            <Form.Label>Ввод</Form.Label>
                            <br />
                            <TextInput bind:value={task.tests[i].input} is="textarea"/>
                        </div>
                        <div class="test-input">
                            <Form.Label>Ожидаемый вывод</Form.Label>
                            <br />
                            <TextInput bind:value={task.tests[i].expectedOutput} is="textarea"/>
                        </div>
                        <div class="test-input">
                            <Form.Label>Видимый тест?</Form.Label>
                            <Switch on:click={() => task.tests[i].visible = !task.tests[i].visible}/>
                        </div>
                    </Container>
                </Container>
            {/each}
            <Button on:click={() => {task.tests = [...task.tests, {input: '', expectedOutput: '', visible: false}]}}>Добавить тест</Button>
        </Container>
        <br /><br /><br />
        <Button on:click={createTask}>Создать задачу</Button>
    </Container>
</main>