<script context="module" >import { make_scoped_store } from "../../../stores/scoped";
import { state } from "../../../stores/state";
export const CONTEXT_FORM_ID = make_scoped_store("form-id");
export const CONTEXT_FORM_NAME = make_scoped_store("form-name");
export const CONTEXT_FORM_STATE = make_scoped_store("form-state", undefined, (default_value) => state(default_value));
</script>

<script >import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();
export let logic_id = undefined;
export let logic_name = undefined;
export let logic_state = undefined;
const _form_id = CONTEXT_FORM_ID.create(logic_id);
const _form_name = CONTEXT_FORM_NAME.create(logic_name);
const _form_state = CONTEXT_FORM_STATE.create(logic_state);
function on_state_property_update(state) {
    if (_form_state && state !== $_form_state) {
        $_form_state = state;
        dispatch("change");
    }
}
function on_state_store_update(state) {
    if (state !== logic_state) {
        logic_state = state;
        dispatch("change");
    }
}
$: if (_form_id)
    $_form_id = logic_id;
$: if (_form_name)
    $_form_name = logic_name;
$: if (logic_state !== undefined)
    on_state_property_update(logic_state);
$: if (_form_state)
    on_state_store_update($_form_state ?? logic_state);
</script>

<slot />
