<script context="module" >import { make_scoped_store } from "../../../stores/scoped";
export const CONTEXT_POPOVER_ID = make_scoped_store("popover-id");
export const CONTEXT_POPOVER_STATE = make_scoped_store("popover-state");
export const CONTEXT_POPOVER_FOCUS_TARGET = make_scoped_store("popover-focus-target");
export const CONTEXT_POPOVER_LOADING = make_scoped_store("popover-loading");
export const CONTEXT_POPOVER_VARIATION = make_scoped_store("popover-variation");
</script>

<script >import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();
export let logic_id = undefined;
export let logic_state = undefined;
export let focus_target = undefined;
export let loading = undefined;
export let variation = undefined;
const _popover_id = CONTEXT_POPOVER_ID.create(logic_id);
const _popover_state = CONTEXT_POPOVER_STATE.create(logic_state);
const _popover_focus_target = CONTEXT_POPOVER_FOCUS_TARGET.create(focus_target);
const _popover_loading = CONTEXT_POPOVER_LOADING.create(loading);
const _popover_variation = CONTEXT_POPOVER_VARIATION.create(variation);
function on_state_property_update(state) {
    if (_popover_state && state !== $_popover_state) {
        $_popover_state = state;
        dispatch("change");
    }
}
function on_state_store_update(state) {
    if (state !== logic_state) {
        logic_state = state;
        dispatch("change");
    }
}
$: if (_popover_id)
    $_popover_id = logic_id;
$: if (_popover_focus_target)
    $_popover_focus_target = focus_target;
$: if (_popover_loading)
    $_popover_loading = loading;
$: if (_popover_variation)
    $_popover_variation = variation;
$: if (logic_state !== undefined)
    on_state_property_update(logic_state);
$: if (_popover_state)
    on_state_store_update($_popover_state ?? logic_state);
</script>

<slot />
